import { Card, Col, Container, Row, Button } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import React from 'react';
import { evaluate } from 'mathjs';

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      value: ['0'],
    };

    this.handleClear = this.handleClear.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.handleResult = this.handleResult.bind(this);
  }

  handleClear() {
    this.setState({
      value: ['0'],
    });
  }

  handleResult() {
    this.setState(state => ({
      value: [evaluate(state.value.join(' ')).toString()],
    }));
  }

  handleClick(event) {
    const input = event.target.innerHTML;
    const lastInput = this.state.value[this.state.value.length - 1];
    const preLastInput = this.state.value[this.state.value.length - 2];
    const operatorRegex = /(\+|-|\*|\/)$/;
    
    if (operatorRegex.test(input)) {
      if (input === '-' && operatorRegex.test(lastInput)) {
        if (operatorRegex.test(preLastInput)) {
          return;
        }

        this.setState(state => ({
          value: [...state.value, input],
        }));
        return;
      }

      if (this.state.value.length === 0) {
        return;
      }

      if (operatorRegex.test(lastInput) && preLastInput && operatorRegex.test(preLastInput)) {
        this.setState(state => ({
          value: [...state.value.slice(0, state.value.length - 2), input],
        }));
        return;
      }

      if (operatorRegex.test(lastInput)) {
        this.setState(state => ({
          value: [...state.value.slice(0, state.value.length - 1), input],
        }));
        return;
      }

      this.setState(state => ({
        value: [...state.value, input],
      }));
      return;
    }

    if (operatorRegex.test(lastInput)) {
      if (input === '.') {
        return;
      }

      if (operatorRegex.test(preLastInput)) {
        this.setState(state => ({
          value: [...state.value.slice(0, state.value.length - 1), state.value[state.value.length - 1] + input],
        }));
        return;
      }

      this.setState(state => ({
        value: [...state.value, input],
      }));
      return;
    }

    if (lastInput === '0') {
      if (input === '0') {
        return;
      }

      if (input === '.') {
        this.setState(state => ({
          value: [...state.value.slice(0, state.value.length - 1), state.value[state.value.length - 1] + '.'],
        }));
        return;
      }

      this.setState(state => ({
        value: [...state.value.slice(0, state.value.length - 1), input],
      }));
      return;
    }

    if (lastInput === '-0') {
      if (input === '0') {
        return;
      }

      if (input === '.') {
        this.setState(state => ({
          value: [...state.value.slice(0, state.value.length - 1), state.value[state.value.length - 1] + '.'],
        }));
        return;
      }

      this.setState(state => ({
        value: [...state.value.slice(0, state.value.length - 1), '-' + input],
      }));
      return;
    }

    if (input === '.') {
      if (this.state.value.length === 0
        || this.state.value[this.state.value.length - 1].includes('.')) {
        return;
      }

      this.setState(state => ({
        value: [...state.value.slice(0, state.value.length - 1), state.value[state.value.length - 1] + '.'],
      }));
      return;
    }

    this.setState(state => ({
      value: [...state.value.slice(0, state.value.length - 1), state.value[state.value.length - 1] ? state.value[state.value.length - 1] + input : input],
    }));
  }

  render() {
    return (
      <Container style={{ paddingTop: '20px', paddingBottom: '20px' }}>
        <Card>
          <Card.Body>
            <Row style={{ marginBottom: '10px' }}>
              <Col xs={9}>
                <pre
                  id='display'
                  style={{
                    textAlign: 'right',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'flex-end',
                    height: '100%',
                    fontWeight: 'bold',
                  }}
                >
                  {this.state.value.join(' ')}
                </pre>
              </Col>
              <Col>
                <Button onClick={this.handleResult} id='equals' style={{ width: '100%' }}>=</Button>
              </Col>
            </Row>
            <Row style={{ marginBottom: '10px' }}>
              <Col xs={3}>
                <Button onClick={this.handleClick} variant='secondary' id='seven' style={{ width: '100%' }}>7</Button>
              </Col>
              <Col xs={3}>
                <Button onClick={this.handleClick} variant='secondary' id='eight' style={{ width: '100%' }}>8</Button>
              </Col>
              <Col xs={3}>
                <Button onClick={this.handleClick} variant='secondary' id='nine' style={{ width: '100%' }}>9</Button>
              </Col>
              <Col xs={3}>
                <Button onClick={this.handleClick} id='divide' style={{ width: '100%' }}>/</Button>
              </Col>
            </Row>
            <Row style={{ marginBottom: '10px' }}>
              <Col xs={3}>
                <Button onClick={this.handleClick} variant='secondary' id='four' style={{ width: '100%' }}>4</Button>
              </Col>
              <Col xs={3}>
                <Button onClick={this.handleClick} variant='secondary' id='five' style={{ width: '100%' }}>5</Button>
              </Col>
              <Col xs={3}>
                <Button onClick={this.handleClick} variant='secondary' id='six' style={{ width: '100%' }}>6</Button>
              </Col>
              <Col xs={3}>
                <Button onClick={this.handleClick} id='multiply' style={{ width: '100%' }}>*</Button>
              </Col>
            </Row>
            <Row style={{ marginBottom: '10px' }}>
              <Col xs={3}>
                <Button onClick={this.handleClick} variant='secondary' id='one' style={{ width: '100%' }}>1</Button>
              </Col>
              <Col xs={3}>
                <Button onClick={this.handleClick} variant='secondary' id='two' style={{ width: '100%' }}>2</Button>
              </Col>
              <Col xs={3}>
                <Button onClick={this.handleClick} variant='secondary' id='three' style={{ width: '100%' }}>3</Button>
              </Col>
              <Col xs={3}>
                <Button onClick={this.handleClick} id='subtract' style={{ width: '100%' }}>-</Button>
              </Col>
            </Row>
            <Row style={{ marginBottom: '10px' }}>
              <Col xs={6}>
                <Button onClick={this.handleClick} variant='secondary' id='zero' style={{ width: '100%' }}>0</Button>
              </Col>
              <Col xs={3}>
                <Button onClick={this.handleClick} variant='secondary' id='decimal' style={{ width: '100%' }}>.</Button>
              </Col>
              <Col xs={3}>
                <Button onClick={this.handleClick} id='add' style={{ width: '100%' }}>+</Button>
              </Col>
            </Row>
            <Row>
              <Col>
                <Button onClick={this.handleClear} variant='danger' id='clear' style={{ width: '100%' }}>Clear</Button>
              </Col>
            </Row>
          </Card.Body>
        </Card>
      </Container>
    );
  }
}

export default App;
